.search{margin:10px 0 20px}.search .stock-input{margin:5px auto;width:20vw;min-width:220px}.search .search-button{background-color:#40576F;color:white;margin:5px}.chips .MuiBadge-badge{color:#40576F}.chips .badge{margin:10px}.chips .chip{background-color:#40576F;color:white;margin:5px}.chips .chip.active-chip{background-color:#039be5}.tweets{width:60vw;margin:0 auto}.tweets .card{border-radius:0;text-align:left}.tweets .card .MuiCardContent-root div{display:grid;grid-template-areas:'picture namearea' 'picture text';grid-template-columns:20% 80%}.tweets .picture{grid-area:picture;display:grid;justify-content:center}.tweets .namearea{grid-area:namearea}.tweets .name{font-weight:bold}.tweets .username{color:#657786}.tweets .text{grid-area:text}@media only screen and (max-width: 960px){.tweets{width:75vw}.search .stock-input{width:75vw}}@media only screen and (max-width: 450px){.tweets{width:90vw}.tweets .picture{justify-content:end}.search .stock-input{width:85vw}}

.xkcd .loading-logo {
  height: 70vh;
}

.slideshow {
  display: grid;
  justify-content: space-evenly;
  grid-template-columns: 32% 32% 32%;
  height: 74vh;
  margin: 0 5px;
}

.panel-card {
  height: 70vh;
}

.slideshow img {
  max-width: 100%;
  max-height: 50vh;
}

.xkcd-footer nav {
  display: grid;
  justify-content: space-evenly;
  grid-template-columns: 23% 23% 23% 23%;
}

.xkcd-footer nav button {
  width: 30%;
  margin: 0 auto;
  text-align: center; 
  padding: 10px 0;
  font-size: 17px;
  font-weight: bold;
  background-color: #6E7B91;
  color: white;
  cursor: pointer;
  box-shadow: 0px 3px 1px -2px rgba(0,0,0,0.2),0px 2px 2px 0px rgba(0,0,0,0.14),0px 1px 5px 0px rgba(0,0,0,0.12);
  border-radius: 7px;
  border: 0;
  font-feature-settings: "c2sc";
  font-variant: small-caps;
}

.credit {
  padding-top: 20px;
  font-size: 17px;
}

@media only screen and (max-width: 960px) {
  .xkcd .loading-logo {
    height: 50vh;
  }

  .slideshow {
    grid-template-columns: 100%;
    height: auto;
  }

  .xkcd-footer nav {
    grid-template-columns: 48% 48%;
    margin: 10px auto;
  }

  .xkcd-footer nav button {
    width: 50%; 
    margin: 10px auto;
  }
}

@media only screen and (max-width: 450px) {
  .xkcd .loading-logo {
    height: 30vh;
  }
}
